@font-face {
  font-family: "HelveticaLight";
  src: url("../fonts/HelveticaLight/HelveticaLight.eot");
  src: url("../fonts/HelveticaLight/HelveticaLight.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaLight/HelveticaLight.woff") format("woff"), url("../fonts/HelveticaLight/HelveticaLight.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "HelveticaMedium";
  src: url("../fonts/HelveticaMedium/HelveticaMedium.eot");
  src: url("../fonts/HelveticaMedium/HelveticaMedium.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaMedium/HelveticaMedium.woff") format("woff"), url("../fonts/HelveticaMedium/HelveticaMedium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  font-size: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 1em /* 16px */;
  line-height: 1.25 /* 20px */;
  font-family: "HelveticaLight", sans-serif;
  color: #333;
}
.page-wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.empty {
  height: 100px;
}
.cf:before,
.row:before,
.cf:after,
.row:after {
  display: table;
  content: " ";
  clear: both;
}
.container {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
.row {
  margin-left: -15px;
  margin-right: -15px;
}
h1,
h2,
h3 {
  font-family: "HelveticaMedium", sans-serif;
  font-weight: normal;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
.logo {
  margin: 0 auto;
  padding: 0;
  display: block;
  max-width: 320px;
  margin-top: 15px;
  margin-bottom: 15px;
}
@media screen and (min-width: 1200px) {
  .logo {
    margin-left: 0;
    margin-right: 0;
  }
}
.logo__img {
  margin: 0;
  padding: 0;
  display: block;
  width: 100%;
  height: auto;
  max-height: 70px;
}
.intro {
  background-image: linear-gradient(45deg, #eecfba, #c5dde8);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.intro__left {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 100px;
  position: relative;
}
@media screen and (min-width: 1200px) {
  .intro__left {
    float: left;
    width: 66.6666666667%;
    padding-top: 160px;
  }
}
.intro__right {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
@media screen and (min-width: 1200px) {
  .intro__right {
    position: relative;
    float: right;
    width: 33.3333333333%;
  }
}
.info__title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  margin-bottom: 20px;
  text-align: center;
  color: #fff;
}
@media screen and (min-width: 1200px) {
  .info__title {
    text-align: left;
    color: #333;
  }
}
.info__text {
  margin: 0;
  padding: 0;
  text-align: justify;
  color: #fff;
}
@media screen and (min-width: 1200px) {
  .info__text {
    color: #333;
  }
}
.service {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 40px;
}
.service__item {
  margin: 0;
  padding: 0;
  float: left;
  width: 50%;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  text-align: center;
  margin-bottom: 40px;
}
@media screen and (min-width: 1200px) {
  .service__item {
    width: 25%;
  }
}
.service__link {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.service__img {
  margin: 0;
  padding: 0;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 64px;
  height: auto;
  transition: 0.25s ease max-width;
}
.service__title {
  margin: 0;
  padding: 0;
  margin-top: 20px;
  margin-bottom: 20px;
  transition: 0.25s ease color;
  height: 25px;
  font-size: 18px;
  white-space: nowrap;
}
@media screen and (min-width: 1200px) {
  .service__title {
    font-size: 20px;
  }
}
.service__text {
  margin: 0;
  padding: 0;
  max-height: 100px;
  overflow: hidden;
  transition: 0.25s ease color;
}
@media screen and (min-width: 1200px) {
  .service__link:hover ~ .service__title,
  .service__link:hover ~ .service__text {
    color: #8b0000;
  }
  .service__link:hover ~ .service__img {
    max-width: 74px;
  }
}
.image {
  position: relative;
  overflow: hidden;
}
.image_square {
  padding-top: 168.75%;
  min-height: 100vh;
}
.image__img {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%);
  animation: fadeIn 1s ease;
}
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.footer {
  position: relative;
  min-height: 100px;
  margin-top: -100px;
  background-color: #222;
}
.footer__copyright {
  text-align: center;
  line-height: 100px;
  color: #fff;
}
.footer__copyright span {
  margin: 0;
  padding: 0;
  margin-left: 5px;
}
.page {
  background-image: linear-gradient(45deg, #eecfba, #c5dde8);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}
.page__left {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 100px;
  position: relative;
}
@media screen and (min-width: 1200px) {
  .page__left {
    float: left;
    width: 66.6666666667%;
    padding-top: 160px;
  }
}
@media screen and (min-width: 1200px) {
  .page__right {
    float: right;
    width: 33.3333333333%;
  }
}
.about {
  margin-bottom: 40px;
}
.about__title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  margin-bottom: 20px;
  text-align: center;
  color: #fff;
}
.about__title_small {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}
@media screen and (min-width: 1200px) {
  .about__title {
    text-align: left;
    color: #333;
  }
}
.about__subtitle {
  margin: 0;
  padding: 0;
  text-align: justify;
  color: #fff;
}
@media screen and (min-width: 1200px) {
  .about__subtitle {
    color: #333;
  }
}
.about__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: 20px;
}
.about__list-item {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  text-align: center;
  color: #fff;
}
.about__list-item:last-of-type {
  margin-bottom: 0;
}
@media screen and (min-width: 1200px) {
  .about__list-item {
    text-align: left;
    color: #333;
  }
}
.gallery {
  margin: 0;
  padding: 0;
  list-style: none;
}
.gallery__item {
  margin: 0;
  padding: 0;
  float: left;
  width: 100%;
  position: relative;
  overflow: hidden;
  padding-top: 50%;
}
@media screen and (min-width: 1200px) {
  .gallery__item {
    width: 50%;
  }
}
.gallery__img {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  max-height: 100%;
  transform: translate(-50%, -50%);
  animation: fadeIn 1s ease;
}
.gallery__img_portrait {
  max-height: none;
  max-width: 100%;
}
.btn {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  display: inline-block;
  vertical-align: top;
  padding: 25px;
  color: #333;
  background-color: transparent;
  border: 1px solid #222;
  border-radius: 10px;
  text-decoration: none;
  transition: 0.25s ease all;
}
.btn_on-main {
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 1;
  color: #fff;
  background-color: #222;
  border-color: #fff;
  animation: fadeIn 1s ease;
}
.btn_on-main:hover {
  color: #333;
  background-color: #fff;
  border-color: #fff;
}
